const getRadioValue = (name) => {
    const radio = document.getElementsByName(name);
    let val;
    Object.keys(radio).forEach((obj, i) => {
        if (radio[i].checked) {
            val = radio[i].value;
        }
    });
    return val;
};

document.getElementById('module').addEventListener('change', (e) => {
   if(getRadioValue('direction') === 'left') {
       nav.classList.add('show-left')
   } else {
       nav.classList.remove('show-left')
   }
    if(getRadioValue('direction') === 'right') {
        nav.classList.add('show-right')
    } else {
        nav.classList.remove('show-right')
    }
    if(getRadioValue('direction') === 'top') {
        nav.classList.add('show-top')
    } else {
        nav.classList.remove('show-top')
    }
    if(getRadioValue('direction') === 'bottom') {
        nav.classList.add('show-bottom')
    } else {
        nav.classList.remove('show-bottom')
    }
});


const menuBtn = document.getElementById('menuToggle'),
    nav = document.getElementById('mainNav');

menuBtn.addEventListener('click', function () {
    toggleClass(nav, 'active')
});

function toggleClass(element, tclass) {
    let classes = element.className,
        pattern = new RegExp(tclass),
        hasClass = pattern.test(classes);

    classes = hasClass ? classes.replace(pattern, '') : classes + ' ' + tclass;
    element.className = classes.trim();
};


const closeMenu = document.getElementById('close-nav');
closeMenu.addEventListener('click', function () {
    toggleClass(nav, 'active')
});

const mq = window.matchMedia( "(min-width: 768px)" );

if (matchMedia) {
    const mq = window.matchMedia("(min-width: 768px)");
    mq.addListener(WidthChange);
    WidthChange(mq);
}

function WidthChange(mq) {
    if (mq.matches) {
        nav.classList.remove('show-left') ||
        nav.classList.remove('show-right') ||
        nav.classList.remove('show-top') ||
        nav.classList.remove('show-bottom')
    }
}

